body {
  margin: 0;
  font-family: "Open Sans","Merriweather",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* flex */
.flex { display: flex; }
.horizontal { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }
.flex-6 { flex: 6; }

.space-between {
  justify-content: space-between;
}
.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-start {
  justify-content: flex-start;
  align-items: center;
}
.flex-end {
  justify-content: flex-end;
  align-items: center;
}

.container, .container-big {
  width: calc(100%-20px);
  padding: 0 10px;
  max-width: 1440px;
  margin: 0 auto;
}
.container {
  max-width: 800px;
}

.cursor { cursor: pointer; }

/* componentes */
.Header > div > div {
  min-height: 70px;
}
.input-pesquisa {
  width: 70%;
  border: 0;
  color: #333;
  font-size: 0.9rem;
  border-bottom: 2px solid #444;
  padding: 3px 0;
}
.button-pesquisa {
  border: 0;
  background: transparent;
  font-size: 1rem;
}
.numero-sacola {
  color: #FEFEFE;
  background-color: #333;
  width: 20px;
  height: 20px;
  font-size: 0.85rem;
  border-radius: 50px;
  margin-top: 20px;
  margin-left: -5px;
}

.Categorias {
  border-bottom: rgb(148, 148, 148) 1px solid;
}
.categoria {
  padding: 8px 10px;
  cursor: pointer;
}
.categoria:hover, .categoria.negrito, .categoria.outlet {
  font-weight: bold;
}
.categoria.outlet {
  color: rgb(219, 14, 14);
}

.breadcrumbs {
  padding: 10px 0;
  color: #444;
  font-size: 0.8rem;
}
.mini-pic {
  cursor: pointer;
  opacity: 0.6;
}
.mini-pic-active, .mini-pic:hover {
  opacity: 1;
}

.titulo h2 {
  font-size: 1.8rem;
  margin: 0;
}
.titulo span {
  font-size: 0.8rem;
  margin-top: 0;
  color: #444;
}

.preco h2 {
  margin: 0;
  font-size: 1.6rem;
  margin-bottom: -5px;
}
.preco span {
  font-size: 0.9rem;
  color: #666;
}

.caracteristicas {
  margin: 20px 0;
  font-size: 0.9rem;
  color: #333;
}

.opcao {
  display: flex;
  flex-direction: column;
  margin: 15px 0; 
}

.opcao-display {
  border: 1px solid #bbbb;
  width: 25px;
  height: 25px;
  padding: 2px;

  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-right: 5px;
}
.preto {
  background-color: #000;
}
.opcao-titulo {
  font-size: 0.95rem;
  padding: 5px 0;
}
.opcao-display:hover, .opcao-display-active {
  border-color: #222;
}

.aviso-opcao {
  color: #777;
  font-size: 0.75rem;
  text-decoration: underline;
  padding-bottom: 5px;
  cursor: pointer;
}

.button-cta {
  border: 0;
  background-color: rgb(228, 25, 25);
  font-size: 1.5rem;
  padding: 12px 40px;
  color: #FFF;
  font-weight: bold;
  margin: 15px 0 20px;
  cursor: pointer;
}
.button-cta:hover {
  background-color: rgb(194, 8, 8);
}

.hr {
  width: 90%;
  height: 2px;
  background-color: #dddd;
}

.cep-container {
  margin-top: 15px;
}
.cep-titulo {
  font-size: 0.9rem;
  color: #444;
}
.input-cep {
  font-size: 0.9rem;
  margin: 10px 0;
  padding: 5px 10px;
  width: 150px;
}
.button-cep {
  background-color: #444;
  color: #eee;
  font-size: 0.9rem;
  border: 0;
  padding: 5px;
  margin: 10px 0;
  font-weight: bold; 
  cursor: pointer;
}
.button-cep:hover {
  background-color: #333;
}

.Avaliacoes {
  padding: 50px 0;
}
.avaliacao {
  padding: 20px;
  margin: 20px;
  border-radius: 10px;
  border: 0;
  box-shadow: 0px 0px 5px #aaaa;
}
.avaliacao-msg {
  min-height: 100px;
}

.Trocas {
  padding: 20px 0;
}

.Institucional {
  padding: 40px 10px;
  background-color: #171717;
  color: #EEE;
}
.logo-footer {
  padding: 15px 0;
  max-width: 120px;
}
.ig i, 
.institucional i {
  font-size: 1.1rem;
  margin: 3px;
  cursor: pointer;
}
.institucional span {
  cursor: pointer;
}
.institucional span:hover {
  text-decoration: underline;
}
.ig-single {
  min-height: 150px;
  margin: 2px;
  cursor: pointer;
}
.input-news {
  margin: 5px 0;
  padding: 5px 8px;
  font-size: 0.9rem;
  background-color: #eee;
  border: 0;
}
.button-news {
  border: 0;
  background-color: #555;
  color: #FEFEFE;
  padding: 8px 20px;
  font-size: 0.9rem;
  cursor: pointer;
}
.button-news:hover {
  background-color: #333;
}

.Footer {
  color: #FEFEFE;
  background-color: #111;
  padding: 20px 0;
  font-size: 0.8rem;
}

.no-mb { display: block; }
.no-mb-flex { display: flex; }
.only-mb, .only-mb-flex { display: none; }
@media screen and (max-width: 800px){
  .overflow-x { overflow-x: scroll; }
  .no-mb, .no-mb-flex { display: none; }
  .only-mb { display: block; }
  .only-mb-flex { display: flex; }

  .button-cta { width: 100%; }

  .opcoes { display: flex; }
  .opcao { flex: 1; }

  .titulo { margin-top: 10px; }

  .ig i {
    font-size: 20px;
    margin-bottom: 10px;
  }

  .wrap { flex-wrap: wrap; }
  .ig-single { flex-basis: 30%; }

  .categoria { padding-top: 20px; }
}